import { ComponentProps } from 'react';
import { tv } from 'tailwind-variants';

type BriefBoxProps = ComponentProps<'div'> & {
  value: number;
  secondaryValue?: number;
  title: 'sales' | 'balance' | 'pending' | 'mission';
};

const briefBox = tv({
  base: 'flex flex-col w-full min-w-[220px] border border-gray-500 rounded-lg p-4 bg-black/30 text-white',
});

export function BriefBoxComponent({ value, secondaryValue, title, ...props }: BriefBoxProps) {
  return (
    <div className={briefBox()} {...props}>
      {title === 'sales' && (
        <>
          <div className="flex justify-between items-center">
            <h3 className="font-bold text-lg">Vendas</h3>
            <span className="text-sm text-gray-400">ontem</span>
          </div>
          <div className="flex justify-between items-center mt-2">
            <span className="text-xl font-bold text-cyan-400">R$ {value.toFixed(2)}</span>
            {secondaryValue !== undefined && (
              <span className="text-xl font-bold text-gray-200">R$ {secondaryValue.toFixed(2)}</span>
            )}
          </div>
        </>
      )}

      {title === 'balance' && (
        <>
          <h3 className="font-bold text-lg">Saldo disponível</h3>
          <span className="text-xl font-bold text-gray-200 mt-2">R$ {value.toFixed(2)}</span>
        </>
      )}

      {title === 'pending' && (
        <>
          <h3 className="font-bold text-lg">Pendente</h3>
          <span className="text-xl font-bold text-gray-200 mt-2">R$ {value.toFixed(2)}</span>
        </>
      )}

      {title === 'mission' && (
        <>
          <h3 className="font-bold text-lg">Missão diária</h3>
          <span className="text-sm px-2 py-1 text-gray-400">Concluído</span>
          <span className="text-xl font-bold">{value} de {secondaryValue}</span>
        </>
      )}
    </div>
  );
}
